@import "main-module";

.modal {
    width: 100%;
    max-width: 836px;
    margin: 0 auto;
    padding: 100px 0;

    display: none;

    &.fancybox__content {
        background-color: transparent;
        @include below(500px) {
            padding: 0;
        } 
    }

    .f-button:not(.f-button_custom) {
        display: none !important;
    }
    .f-button.f-button_custom {
        background: transparent !important;
        top: 20px;
        right: 20px;

        @include below(500px) {
            top: 10px;
            right: 10px;
        }

        svg {
            width: 20px;
            height: 20px;
        }
    }
    &_telegram {
        .modal__container {
            max-width: 836px;
            margin: 0 auto;
        }
    }

    &__container {
        padding: 60px;
        background-color: $color_white;
        border-radius: 20px;
        border: 1px solid #000;
        position: relative;


        @include below(1024px) {
            padding: 40px;
        }

        @include below(768px) {
            padding: 20px;
        }
    }

    &__wrap {
        display: flex;
        align-items: center;

        @include below(768px) {
            flex-direction: column;
        }
    }

    &__img {
        width: 100%;
        max-width: 248px;
        margin-right: 60px;

        @include below(768px) {
            margin: 0 0 40px;
        }

        @include below(540px) {
            max-width: 200px;
            height: 180px;
            transform: translateY(-100px);
        }

        img {
            height: auto;
        }
    }

    &__top-title {
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: 0.02em;
        color: $color_blue;

        @include below(540px) {
            font-size: 20px;
            text-align: center;
        }
    }

    &__text {
        font-size: 14px;
        line-height: 130%;
        margin: 16px 0;

        @include below(540px) {
            text-align: center;
        }
    }

    &__btn {
        display: inline-flex;
        align-items: center;
        justify-content: center;

        @include below(540px) {
            display: flex;
        }

        svg {
            width: 18px;
            height: 18px;
            margin-right: 12px;
        }
    }

    &__bottom {
        margin-top: 40px;

        @include below(768px) {
            margin-top: 30px;
        }
    }

    &__bottom-title {
        font-size: 14px;
        line-height: 130%;
        margin-bottom: 16px;

        @include below(540px) {
            text-align: center;
        }
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        margin: -5px;

        @include below(540px) {
            align-items: center;
            justify-content: center;
        }
    }

    &__link {
        margin: 5px;
        padding: 8px 12px;
        font-weight: 500;
        font-size: 12px;
    }
}