@import "main-module";

.section {
    &__title {
        font-family: $accentFont;
        font-weight: 500;
        font-size: 32px;
        line-height: 110%;
        margin-bottom: 24px;
        letter-spacing: 0.01em;

        @include below(1024px) {
            font-size: 24px;
        }
    }   
    
    &__text {
        font-family: $accentFont;
        font-size: 16px;
        line-height: 130%;
        &:not(:last-child) {
            margin-bottom: 24px; 
        }
    }
}